:root {
  --primary: #242424;
  --secondary: #F1AE71;
  --tertiary: #e49e5d;
  --quaternary: #f18b71;
  --quinary : #e47a5d;
  --light: #EBEADD;
  --dark: #242424;
  --error: #B12704;
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  background-color: #EBEADD;
  background-color: var(--light);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: #242424;
  color: var(--primary);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #B12704;
  color: var(--error);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: #242424;
  background: var(--primary);
}

header a {
  color: #EBEADD;
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #F1AE71;
  background-color: var(--secondary);
}

button:hover {
  background-color: #e49e5d;
  background-color: var(--tertiary);
  transition: background-color 0.3s ease, -webkit-transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  outline: none;
  border-radius: 3px;
}

input:focus {
  border: 1px solid #F1AE71;
  border: 1px solid var(--secondary);
  box-shadow: 0 8px 12px #24242437;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: #B12704;
  color: var(--error);
}
.card {
  position: relative;
  box-sizing: border-box;
  padding: 20px;
  margin: 10px;
  background-color: var(--light);
  border-radius: 5px;
  transition: all 0.3s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(25% - 20px); 
  max-width: calc(25% - 20px);
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 8px 12px #24242437;
}

.card:hover a {
  text-decoration: none;
}

.img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.img-container img {
  width: 100%;
  height: auto;
  display: block;
}

.product-info {
  color: var(--primary);
  font-family: 'Barlow', sans-serif;
}

.product-name {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 8px;
  font-family: 'Barlow', sans-serif;
}

.product-price {
  font-size: 16px;
  margin-bottom: 16px;
  font-family: 'Helvetica', sans-serif;
}

/* Tablet devices (768px and up) */
@media screen and (max-width: 1024px) {
  .card {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(50% - 20px); /* 50% width minus margins */
    max-width: calc(50% - 20px);
  }

  .product-name {
    font-size: 20px;
  }
  
  .product-price {
    font-size: 18px;
  }
}

/* Mobile devices (767px and below) */
@media screen and (max-width: 480px) {
  .card {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(100% - 20px); /* 100% width minus margins */
    max-width: calc(100% - 20px);
    /* margin: 0px 10px; */
  }

  .product-name {
    font-size: 18px;
  }
  
  .product-price {
    font-size: 16px;
  }
}

.category-menu {
  font-family: 'Barlow', sans-serif;
  color: var(--primary);
  margin: 30px 10px 10px 10px;
  padding: 10px 10px;
  cursor: pointer;
  border-bottom: var(--secondary) 5px solid;
}

.category-menu:hover {
  border-bottom: var(--tertiary) 5px solid;
  transition: background-color 0.3s ease, -webkit-transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
}

.whole-category {
  width: 100%;
}

.category-btn {
  cursor: pointer;
  background-color: var(--secondary);
  color: var(--primary);
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 25px;
  margin: 0 5px 5px 10px;
  border-radius: 3px;
}

.category-btn:hover {
  background-color: var(--tertiary);
  transition: background-color 0.3s ease, -webkit-transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease;
  transition: background-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

/* Tablet devices (768px and up) */
@media screen and (max-width: 1024px) {
  .category-btn {
    font-size: 15px;
    padding: 10px 20px;
    margin: 0 5px 5px 10px;
  }

  .category-menu {
    font-size: 24px;
  }
}

/* Mobile devices (767px and below) */
@media screen and (max-width: 480px) {
  .category-btn {
    font-size: 12px;
    padding: 10px 20px;
    margin: 0 5px 5px 10px;
  }

  .category-menu {
    font-size: 22px;
    text-align: center;
  }
}

.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 40%;
  min-height: 20%;
  max-height: 100%;
  background-color: var(--light);
  padding: 32px;
  box-shadow: 0 8px 12px #24242437;
  border-bottom-left-radius: .5rem;
  overflow-y: scroll;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--primary);
  padding-bottom: .5rem;
  margin: 1rem 0;
  font-family: 'Barlow', sans-serif;
}

.cart .close {
  position: absolute;
  top: 0.5px;
  right: 9px;
  cursor: pointer;
  color: var(--primary);
  font-size: 1.5rem;
  overflow-y: scroll;
}

.cart .close:hover {
  text-decoration: none;
  color: var(--error);
}

.cart-closed {
  position: fixed;
  top: 25px;
  right: 25px;
  font-size: 40px;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 2px;
  width: 60px;
  height: 60px;
  text-align: center;
  overflow-y: scroll;
}

.cart-closed span {
  cursor: pointer;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart-overall {
  align-items: center;
  margin: 10px 0;
  width: 100%;
}

.cart-item {
  font-family: 'Helvetica', sans-serif;
  width: 100%;
}

.quantity-remove {
